.hamburger {
    $this: &;
    width: 30px;
    height: 50px;
    display: none;
    justify-content: center;
    align-items: center;
    margin: 0;
    margin-left: 20px;
    transition-property: opacity, filter;
    transition-duration: .15s;
    transition-timing-function: linear;
    font: inherit;
    color: inherit;
    text-transform: none;
    border: 0;
    user-select: none;
    border-radius: 50%;
    cursor: pointer;
    user-select: none;
    @media screen and(max-width: 960px) {
        display: flex;
    }
    &.is-active {
        #{$this}__inner {
            transform: translate3d(0, 8px, 0) rotate(45deg);
        }

        #{$this}__inner::before {
            opacity: 0;
        }

        #{$this}__inner::after {
            transform: translate3d(0, -16px, 0) rotate(-90deg);
        }
    }

    &__box {
        position: relative;
        width: 24px;
        height: 18px;
        display: inline-block;
    }

    &__inner {
        top: 2px;
        display: block;
        top: 2px;
        margin-top: -2px;
        width: 24px;
        height: 2px;
        background-color: var(--main-color);
        position: absolute;
        transition-property: transform, opacity;
        transition-duration: .15s;
        transition-timing-function: ease;

        &::before,
        &::after {
            content: '';
            display: block;
            position: absolute;
            height: inherit;
            background-color: inherit;
            transition-property: transform, opacity;
            transition-duration: .15s;
            transition-timing-function: ease;
        }

        &::before {
            right: 0;
            top: 8px;
            width: 18px;
        }

        &::after {
            top: 16px;
            bottom: -5px;
            width: 24px;
        }
    }
}
