.header {
    $this: &;
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding-top: 30px;
    padding-bottom: 30px;
    transition: padding .3s;
    @media screen and (max-width: 960px) {
        padding-top: 10px;
        padding-bottom: 10px;
        background-color: #fff;
    }
    &__logo {
        flex-shrink: 0;
        display: block;
        width: 100px;
        margin-right: auto;

        .logo__img--dark {
            display: none;
        }

        .logo__img--light {
            display: block;
        }
        @media screen and (max-width: 960px) {
            width: 60px;
        }
    }

    &.is-scrolled {
        padding-top: 10px;
        padding-bottom: 10px;
        background-color: #fff;

        .logo__img--dark {
            display: block;
        }

        .logo__img--light {
            display: none;
        }
    }

    &__inner {
        display: flex;
        align-items: center;
    }
    @media screen and (max-width: 960px) {
        .logo__img--dark {
            display: block!important;
        }
        .logo__img--light {
            display: none!important;
        }
    }
}

.nav {
    $this: &;
    @media screen and (max-width: 960px) {
        display: none;
        height: 0;
        animation: h 1s both;
        .nav__list {
            opacity: 0;
            transition: opacity .3s 1s; 
        }
        @keyframes h {
            0% {
                height: 0;
            }
            100% {
                height: 100vh;
            }
        }
    }
    &.mobile {
        @media screen and (max-width: 960px) {
            display: flex;
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            width: 200px;
            background-color: #fff;
            padding: 20px 10px;
            justify-content: center;
            align-items: center;
            .nav__list {
                opacity: 0;
                animation: op .3s 1s both;
                @keyframes op {
                    0% {
                        opacity: 0;
                    }
                    100% {
                        opacity: 1;
                    }
                }
            }
            .nav__link {
                color: #000
            }
        }
    }
    &.is-active {
        
        #{$this}__link {
            color: var(--dark-color);

            &.is-active,
            &:hover {
                border-bottom-color: var(--main-color);
                ;
            }
        }
    }

    &__list {
        display: flex;
        @media screen and (max-width: 960px) {
            flex-direction: column;
            .nav__item {
                margin-right: auto;
                margin-bottom: 20px;
            }
        }
    }

    &__item:not(:last-of-type) {
        margin-right: 64px;
    }

    &__link {
        color: #fff;
        text-decoration: none;
        border-bottom: 2px solid transparent;
        transition: border-bottom .4s ease;

        &.is-active,
        &:hover {
            border-bottom-color: #fff;
        }
    }
}

.block {
    display: flex;
    align-items: center;
    justify-content: center;
}
