.rest {
    margin-bottom: 20px;

    &__title {
        @include css-lock(30, 120, 600, 1200, margin-bottom);
        margin-left: auto;
        margin-right: auto;
        max-width: 500px;
        text-align: center;
    }

    &__inner {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
        gap: 30px;
    }
}

.card {
    max-width: 407px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;

    &__head {
        aspect-ratio: 1;
        background-color: red;
        margin-bottom: 30px;
    }

    &__img {
        height: 100%;
        display: block;
        object-fit: cover;
        object-position: center;
    }

    &__body {
        padding: 0 10px;
    }

    &__text {
        text-align: center;
    }
}

.section-background {
    z-index: 1;
    position: relative;
    height: 80vh;
    background-repeat: no-repeat;
    background-size: cover;
    color: #fff;

    &::after {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, .5);
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: 100%;
    }
}

.products-nav {
    $this: &;
    padding-bottom: 50px;
    padding-top: 80px;

    &:not(#{$this}--vertical) {
        background-color: #ebf7f8;

        #{$this}__list {
            flex-wrap: wrap;
        }
    }

    &__list {
        display: flex;
        justify-content: space-around;
        grid-gap: 30px;
    }

    &__item {
        display: flex;
        justify-content: center;
    }

    &__icon {
        display: inline-block;
        margin-bottom: 10px;
        fill: var(--main-color)
    }

    &--vertical {
        position: sticky;
        top: 0;
        z-index: 9999;
        max-width: 260px;
        padding: 4% 0;
        height: 100vh;
        flex-direction: column;
        height: 100vh;
        background-color: #fff;

        @media screen and (max-width: 960px) {
            position: static;
            max-width: 100%;
            height: auto;
            padding: 70px 0 20px;

            #{$this}__list {
                flex-wrap: nowrap;
                align-items: flex-start;
                flex-direction: row !important;
            }

            #{$this}__item {
                min-width: 0 !important;
                margin-bottom: 0 !important;
            }
        }

        #{$this}__list {
            flex-direction: column;
            height: 100%;
        }

        #{$this}__icon {
            fill: grey;

            @media screen and (max-width: 960px) {
                width: 75px;
            }
        }

        #{$this}__item {
            flex: 1;

            &:not(:last-of-type) {
                margin-bottom: 10vw;
            }
        }
    }
}

.accordion {
    // display: flex;
    flex-direction: column;
    padding-bottom: 20px;

    &__body.is-open {
        animation: open .5s both;

        @keyframes open {
            0% {
                opacity: 0;
                transform: translate3d(0, 100%, 0)
            }

            100% {
                opacity: 1;
                transform: none;
            }
        }
    }

    &:not(:last-of-type) {
        margin-bottom: 20px;
        border-bottom: 1px solid #c3d1d6;
    }

    &__head {
        position: relative;
        padding-left: 33px;
        margin-bottom: 10px;
        margin-top: 30px;
        cursor: pointer;

        &::after,
        &::before {
            content: '';
            display: block;
            position: absolute;
            box-sizing: border-box;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            left: 11px;
            top: 50%;
            background-color: var(--main-color);
            transition: background-color .2s ease-in-out, opacity .2s ease-in-out;
        }

        &::before {
            height: 2px;
            width: 10px;
        }

        &::after {
            height: 10px;
            width: 2px;
            opacity: 1;
        }

        &.is-open::after {
            opacity: 0;
        }
    }

    &__icon {
        width: 22px;
        height: 22px;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        border: 2px solid var(--main-color);
    }

    &__body {
        display: none;
        padding-left: 33px;

        &.is-open {
            display: block;
        }
    }
}

.grid {
    $this: &;
    display: flex;
    flex-wrap: wrap;

    &__item {
        display: flex;
        flex-wrap: wrap;
        width: 100%;

        &--reverse {
            flex-direction: row-reverse;
        }

        &--light {
            background-color: #ebf7f8;
        }

        &--green {
            background-color: var(--main-color);
            color: #fff;
        }
    }

    &__column {
        flex: 1 1 48%;
        width: auto;
        max-width: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
        // align-items: center;
        padding: 20px;
        aspect-ratio: 1;

        &:not(#{$this}__column--img) {
            padding-left: 10%;
            padding-right: 10%;
        }

        &--img {
            flex-basis: 52%;
            min-width: 400px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;

            @media screen and (max-width: 760px) {
                min-width: 380px;
            }
        }
    }

    &__img {
        display: block;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    &__buttons {
        width: 100%;
        display: flex;

        .button {
            flex-shrink: 0;
        }

        .button+.button {
            margin-left: 15px;
        }
    }
}

.dotted-list {
    $this: &;
    width: 100%;
    margin-bottom: 15px;
    padding-left: 0;
    list-style-type: none;

    &__item {
        margin-bottom: 10px;
    }

    &--white {
        #{$this}__item {
            padding-left: 12px;
            color: #fff;

            &::before {
                top: 7px;
                width: 5px;
                height: 5px;
                background-color: #fff;
            }
        }
    }

    &__item {
        position: relative;
        padding-left: 20px;

        &::before {
            content: '';
            position: absolute;
            top: 5px;
            left: 0;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background-color: #000;
        }
    }
}

.info {
    margin-bottom: 100px;
    margin-top: 100px;

    &__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        gap: 50px;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__img {
        width: 177px;
        height: 177px;
        border-radius: 50%;
    }

    &__text {
        margin-bottom: 20px;
    }

    &__text-block {
        max-width: 880px;
        min-width: 300px;
        flex-grow: 1;
        flex-basis: 60%;
    }
}

.section--text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    aspect-ratio: 10 / 4;
    background-repeat: no-repeat;
    background-size: cover;
    color: #fff;

    @media screen and (max-width: 768px) {
        aspect-ratio: auto;
        min-height: 50vh;
    }
}

.top-section {
    &__icon {
        width: 200px;
        height: auto;
        margin-bottom: 20px;
        fill: #42b6be;
    }

    min-height: 100vh;
}

.product-info {
    &__inner {
        display: flex;

        @media screen and (max-width: 760px) {
            flex-direction: column;
        }
    }

    &__img,
    &__text-block {
        flex: 1 1 50%;
    }

    &__img {
        max-width: 50%;
        align-self: center;

        @media screen and (max-width: 760px) {
            max-width: 100%;
            margin-bottom: 30px;
        }
    }

    &__text-block {
        margin-right: 50px;
        margin-left: 50px;

        @media screen and (max-width: 760px) {
            margin-left: 0;
            margin-right: 0;
        }
    }
}

.table {
    color: #555;

    &__caption {
        font-size: 25px;
        margin-bottom: 15px;
        color: #000;
    }

    &__heading {
        font-size: 30px;
        color: #000;

        strong {
            font-weight: 600;
            font-size: 45px;
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        padding: 10px 20px;

        &:not(:last-of-type) {
            border-bottom: 1px solid #c3d1d6;
        }
    }
}

.info-table {
    &__cell {
        margin-bottom: 70px;

        @media screen and (max-width: 760px) {
            margin-bottom: 45px;
        }
    }
}

.setup {
    &__title {
        margin-bottom: 40px;
        text-align: center;
    }

    &__inner {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        grid-gap: 50px
    }
}

.setup-table {
    &__row {
        justify-content: center;
    }
}

.benefits {
    background-color: #ebf7f8;

    &__inner {

        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        grid-gap: 50px;

        +md {
            grid-template-columns: 1fr;
        }
    }

    &__item {}

    &__title,
    &__icon {
        margin-bottom: 20px;
    }

    &__icon {}

    &__title {}
}

.faq {
    text-align: center;

    &__title {
        margin-bottom: 30px;
        text-align: center;
    }

    &__questions {
        max-width: 700px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
        text-align: left;
    }

}

.accordion {
    color: #555;
}

.products {
    &__top {
        min-height: 95vh;

        &::after {
            background-color: rgba(0, 0, 0, 0.6);
        }
    }

    &__products:not(.product) {
        background-color: transparent;
        width: 100%;

        .products-nav__list {
            justify-content: space-between;
            text-align: center;

            @media screen and (max-width: 768px) {
                flex-wrap: wrap;
            }
        }
    }

    &-nav__icon--big {
        display: block;
        width: 200px;
        height: auto;
        margin-bottom: 30px;

        @media screen and (max-width: 768px) {
            width: 100px;
        }
    }

    &-nav__link {
        text-decoration: none;
    }

    &-nav__item {
        flex-grow: 1;
        // min-width: 300px;
    }
}

.gallery {
    overflow: hidden;

    &::after {
        clear: both;
        display: block;
    }

    &__item {
        width: 100%;
        max-width: 25%;
        float: left;
        cursor: pointer;
        transition: transform .3s;

        &:hover {
            transform: scale(1.1);
        }
    }
}

.block-gallery {
    background-color: #ebf7f8;

    &__dark {
        background-color: var(--dark-color);
    }
}

.slim {
    display: flex;
    padding: 50px;
    min-height: 300px;
    align-items: center;
    background-color: #ebf7f8;
    &--white {
        background-color: #fff;
    }
    &__title {
        margin-bottom: 30px;
    }
    @media screen and (max-width: 768px) {
        flex-direction: column;
        padding: 50px 20px;
        min-height: 0px;

    }

    &__title {
        flex: 0 1 50%;
        width: 100%;
    }

    &__text {
        flex: 0 1 50%;
        margin-right: 50px;

        &--centered {
            flex-basis: auto;
            max-width: 800px;
            margin-left: auto;
            margin-right: auto; 
        }
    }
}

.video-block {
    padding-top: 100px;
    padding-bottom: 100px;
    background-color: var(--dark-color);
    color: #fff;

    &__inner {
        max-width: 600px;
    }

    &__video {
        aspect-ratio: 2/1;
    }
}

.entry {
    position: relative;
    min-height: 100vh;

    @media screen and (max-width: 768px) {
        min-height: 0;
    }

    &__content {
        height: 100%;

        @media screen and (max-width: 960px) {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding: 30px 20px;
            min-height: 60vh;
        }
    }

    &__title {
        max-width: 400px;
        font-size: 60px;
        bottom: 40px;
        margin-left: 290px;
        position: relative;
        z-index: 1;
        color: #fff;

        @media screen and (max-width: 960px) {
            font-size: 40px;
            margin-left: 0;
            bottom: 0;
        }

        @media screen and (max-width: 599px) {
            font-size: 32px;
        }

    }

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;

        @media screen and (max-width: 960px) {
            display: none;
        }
    }
}

.register-modal {
    width: 100%;
    display: none;
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 50px 20px;
    background-color: #fff;
    overflow: scroll;

    display: none;
    height: 0;
    animation: o 1s both;

    .register-modal__inner {
        opacity: 0;
        transition: opacity .3s 1s;
    }

    @keyframes o {
        0% {
            height: 0;
        }

        100% {
            height: 100vh;
        }
    }

    &.is-active {
        display: block;

        .register-modal__inner {
            opacity: 0;
            animation: opacity .3s 1s both;

            @keyframes opacity {
                0% {
                    opacity: 0;
                }

                100% {
                    opacity: 1;
                }
            }
        }

    }

    &__inner {
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
    }

    &__info {
        margin-top: 50px;
        margin-bottom: 50px;
    }

    &__info-inner {
        align-items: center;
    }

    &__img {
        width: 140px;
        height: 140px;
    }

    &__text {
        margin-bottom: 15px;
    }
}

.form {
    $this: &;

    &__group {
        position: relative;
        height: 56px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        &:not(:last-of-type) {
            margin-bottom: 20px;
        }
    }

    &__field {
        position: relative;
        z-index: 1;
        width: 100%;
        padding: 8px 0;
        border-bottom: 1px solid #c3d1d6;
        background-color: transparent;

        &:focus {
            border-bottom: 3px solid #000;
        }
    }

    &__label {
        position: absolute;
        z-index: 0;
        top: 15px;
        text-transform: capitalize;
        transition: top .2s;
    }

    &__field:focus+#{$this}__label {
        top: 0px;
    }
}

.address {
    font-style: normal;

    &__link {
        text-decoration: none;
        color: #557cbf;
    }

    &__item {
        &--separate {
            margin-top: 20px;
        }
    }
}

.close {
    position: absolute;
    right: 50px;
}

.close svg {
    display: block;
    background-color: transparent;
}

.contact-us {
    margin-left: 50px;
}

.contact-us,
.close {
    background-color: transparent;
}

.contact-us svg {
    display: block;
    background-color: transparent;

    path {
        fill: #fff;
    }

    @media screen and (max-width: 960px) {
        background-color: transparent !important;

        path {
            fill: #42b6be;
        }
    }
}

.header.is-scrolled .contact-us {

    svg {
        background-color: #fff;

        path {
            fill: #42b6be;
        }
    }
}

.block-slider {
    &__image {
        width: 100%;
        display: block;
    }
}

.scroll-block {
    position: absolute;
    top: 70%;
    left: 0;
    margin: auto;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;

    &__icon {
        path {
            fill: #fff;
        }
    }
}
.price {
    margin-top: 35px;
    strong {
        font-size: 45px;
    }
}