body {
	// font-size: 200px
	// +css-lock(16, 50, 600, 1000)
	overflow-x: hidden;
	font-family:  Arial, Helvetica, sans-serif;
	font-weight: 300;
	line-height: 1.7; }

a {
	color: inherit; }

h1,h2,h3,h4,h5,h6 {
	line-height: 1.4;
	font-weight: 400;
	font-family: Trocchi;
	text-transform: capitalize; }

h1 {
	@include css-lock(26, 42, 600, 1000);
	@include css-lock(15, 30, 600, 1000, margin-bottom); }
h2 {
	@include css-lock(22, 34, 600, 1000);
	@include css-lock(15, 25, 600, 1000, margin-bottom); }

h3 {
	@include css-lock(18, 28, 600, 1000);
	@include css-lock(15, 20, 600, 1000, margin-bottom); }

h4 {
	@include css-lock(16, 22, 600, 1000);
	@include css-lock(10, 15, 600, 1000, margin-bottom); }

h5 {
	@include css-lock(14, 18, 600, 1000);
	@include css-lock(10, 15, 600, 1000, margin-bottom); }

h6 {
	@include css-lock(12, 14, 600, 1000);
	@include css-lock(5, 15, 600, 1000, margin-bottom); }


img {
	max-width: 100%;
	height: auto; }

.container {
	width: 100%;
	max-width: 1280px;
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto; }

.text {
	@include css-lock(5, 15, 600, 1000, margin-bottom); }

.section {
	&__inner {
		@include css-lock(40, 80, 600, 1000, padding-top);
		@include css-lock(40, 80, 600, 1000, padding-bottom); } }
